import React from 'react';
import s from './users.module.css'
import Paginator from "../common/Paginator/Paginator";
import User from "./User";
import FriendsList from "./../../axios/images/BIG-profile.jpg"


let Users = ({totalItemsCount, pageSize, currentPage, onPageChanged, users, unfollow, follow, followingInProgress}) => {


    return <div className={s.usersStyle}>
        <div className={s.textFriendsList}><img className={s.friendsList} src={FriendsList} alt="Friends list"/>
            <h2>Friends list</h2></div>
        <div className={s.forPaginator}><Paginator totalItemsCount={totalItemsCount} pageSize={pageSize}
                                                   currentPage={currentPage} onPageChanged={onPageChanged}/></div>
        <div className={s.userStyle}>
            {users.map(u => <User key={u.id} user={u} followingInProgress={followingInProgress}
                                  unfollow={unfollow} follow={follow}/>)}
        </div>
    </div>
}

export default Users;